*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.breaker {
  display: block;

  &--mobile {
    @media (max-width: $mobile-width-only) {
      display: none;
    }
  }

  &--tablet {
    @media (min-width: $tablet-width) {
      display: none;
    }
  }

  &--desktop {
    @media (min-width: $desktop-width) {
      display: none;
    }
  }
}
